import { Fragment, FunctionComponent, useState } from "react";
import GridItem from "../Components/Atoms/GridItem";
import gridItems, { Category } from "./productmap";
import Checkbox from "../Components/Molecules/checkbox";





const Products: FunctionComponent = () => {
    const [filters, setFilters] = useState<Category[]>([]);

    const updateFilter = (category: Category) => {

        if (filters.includes(category)) {
            setFilters(filters.filter(x => x !== category))
        } else {
            setFilters([...filters, category])
        }

    }

    console.log(filters)

    return (
        <div style={{ backgroundColor: '#dddddf' }} className="page-container">
            <div className="filter-tags">
                <ul>
                    <li>
                        <Checkbox onChange={ () => updateFilter(Category.Aantrekhulpmiddelen) } label="Aantrek hulpmiddelen" />
                    </li>
                    <li>
                        <Checkbox onChange={ () => updateFilter(Category.VerzorgingHulp) } label="Verzorging" />
                    </li>
                    <li>
                        <Checkbox onChange={ () => updateFilter(Category.Mobiliteitshulpmiddelen) } label="Mobiliteit" />
                    </li>

                    <li>
                        <Checkbox onChange={ () => updateFilter(Category.Overige) } label="Overige" />
                    </li>
                </ul>
            </div>
            <div className="grid-container">
                {gridItems.filter(v => filters.length === 0 || filters.includes(v.category)).map((props) => <GridItem {...props} />)}
                {/* { gridItems.map((props) => <GridItem { ...props } />) } */}
            </div>
        </div>
    );
}

export default Products;
